@use "cams-variables";
@use "colors";
@use "mixins";

:host {
  &::ng-deep {
    .mill-primitive-checkbox {
      &__box {
        border-color: colors.$red-50;
      }
    }
  }
}

.document-list {
  &__upload {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &-title {
      text-align: center;
      padding-bottom: 12px;
      margin: 0;
    }

    &-description {
      text-align: center;
    }

    &-action {
      margin-block-start: 24px;
    }
  }
}

.document-list-wrapper {
  display: flex;
  flex-wrap: wrap;

  .documents-list-uploader {
    margin-right: 16px;
    flex-basis: calc(50% - 16px);
    border-radius: 8px;
    overflow: hidden;
    height: 474px;

    &_border {
      border: 1px solid colors.$grey-30;
    }
  }

  .documents-list-component {
    flex-basis: 50%;
    border: 1px solid colors.$grey-30;
    padding: 12px;
    min-width: 0;
    max-height: 412px;
    overflow-x: auto;
    border-radius: 8px;
  }
}
